<template>
  <el-container class="container-main">
    <el-aside width="auto" class="leftMenu">
      <div class="navLeader">
        <h3 v-if="isCollapse">功能菜单</h3>
        <p @click="goHome">
          <span class="icon iconfont el-icon-extend-shouye1"></span>
        </p>
      </div>
      <Slide :menuList="menuList" :isCollapse="isCollapse" />
    </el-aside>
    <el-main> <router-view></router-view> </el-main>
  </el-container>
</template>

<script>
import Slide from "@/components/common/AppNav";
import { getSlideMenu } from "@/http/AppApi";
export default {
  components: { Slide },
  data() {
    return {
      menuList: [
        { menuHref: "/setting/dictType", menuName: "字典设置" },
        { menuHref: "/setting/sysLog", menuName: "系统日志" },
      ],
      isCollapse: true,
    };
  },
  mounted() {},
  methods: {
    goHome() {},
  },
};
</script>

<style scope>
.container-main {
  height: 100% !important;
}
.el-aside {
  height: 100%;
}
.el-main {
  height: 100%;
  padding: 0;
}
.leftMenu {
  position: relative;
  z-index: 1;
  -webkit-box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.1);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.1);
}
.navLeader {
  display: flex;
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  color: #cf1f00;
  background: #eaeaea;
  border-bottom: 1px solid #e6a23c;
}
.navLeader h3 {
  flex: 1;
  padding-left: 15px;
}
.navLeader p {
  flex: 1;
  line-height: 50px;
  text-align: center;
  cursor: pointer;
}
.navLeader span {
  font-size: 24px;
}
</style>


